import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HttpModule } from '@angular/http';

// import { ModuleWithProviders } from '@angular/core';
import { ActiveRouteGuard } from './auth/activate-route-guard';

import { LoginComponent } from './auth/login/login.component';
import { RegisterComponent } from './auth/register/register.component';
import { ForgetPasswordComponent } from './auth/forget-password/forget-password.component';
import { ResetPasswordComponent } from './auth/reset-password/reset-password.component';
import { TemproryRegisterComponent } from "./auth/temprory-register/temprory-register.component";
export const appRoutes: Routes = [
  {
    path: 'login',
    component: LoginComponent,
    canActivate: [ActiveRouteGuard]
  },
  {
    path: 'register',
    //component: RegisterComponent,
    component: TemproryRegisterComponent,
    canActivate: [ActiveRouteGuard]
  },
  {
    path: 'beta-register',
    component: RegisterComponent,
    canActivate: [ActiveRouteGuard]
  },
  {
    path: '',
    loadChildren: 'app/theme/theme.module#ThemeModule',
  },
  {
    path: 'forgot-password',
    component: ForgetPasswordComponent,
  },
  {
    path: 'forgot-password/reset/:token',
    component: ResetPasswordComponent,
  }
];

@NgModule({
  imports: [ RouterModule.forRoot(appRoutes, { useHash: true}),HttpModule ],
  exports: [ RouterModule,HttpModule ]
})
export class AppRoutingModule { }



